import { Component, OnInit, OnDestroy, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-ContactUs-cmp',
  templateUrl: './ContactUs.component.html'
})

export class ContactUsComponent implements OnInit, OnDestroy {
  test: Date = new Date();
  @ViewChild('contactNgForm') contactNgForm: NgForm;
  contactForm: FormGroup;
  
  showFailAlert = false;
  errorMessage = ''
  showSuccessAlert = false;
  successMessage = ''

  constructor(private router: Router,
    private cdf: ChangeDetectorRef,
    private _formBuilder: FormBuilder,
    private dataService: DataService) {}

  ngOnInit() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];
    setTimeout(function () {
      // after 1000 ms we add the class animated to the login/register card
      // card.classList.remove('card-hidden');
    }, 700);

    this.contactForm = this._formBuilder.group({
      full_name: null,
      email: null,
      phone_no: null,
      subject: null,
      message: null
    });
  }

  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  contact(){
    this.showFailAlert = false;
    this.showSuccessAlert = false;
    this.cdf.detectChanges();
    
    for (let el in this.contactForm.controls) {
      if (this.contactForm.controls[el].errors) {
        console.log(el)
      }
 } 
 alert
    if(this.contactForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields With Correct Format',
        showConfirmButton: false,
        timer: 2500
      })  
      this.cdf.detectChanges()
      return
    }

    const formData = this.contactForm.getRawValue();

    const data = {
      full_name: formData.full_name,
      email: formData.email,
      phone_no: formData.phone_no,
      subject: formData.subject,
      message: formData.message
    }
    this.dataService.adminpost("contactus/store",data).then(
      data=>{
          console.log(data);
          if (data.success == true)
          {
            Swal.fire({
              position: 'center',
              icon: 'success',
              text: data.message,
              showConfirmButton: false,
              timer: 2500
            })  
            this.cdf.detectChanges()
            }
            else {
              Swal.fire({
                position: 'center',
                icon: 'error',
                text: data.message,
                showConfirmButton: false,
                timer: 2500
              })  
              this.cdf.detectChanges()
          }    
      },
      er=> {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: data.message,
          showConfirmButton: false,
          timer: 2500
        })  
        this.cdf.detectChanges()
      }
     
     );
  }
}
