import { Component, OnInit, OnDestroy, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { DataService } from 'src/app/_services/data.services';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-Blogs-cmp',
  templateUrl: './Blogs.component.html'
})

export class BlogsComponent implements OnInit, OnDestroy {
  test: Date = new Date();
  @ViewChild('subscribeNgForm') subscribeNgForm: NgForm;
  subscribeForm: FormGroup;
  showAlert = false;
  errorMessage = null;
  categories = null;
  activeCategoryState = 0;
  blogs = null
  moment: any = moment;

  constructor(private router: Router, 
    private cdf: ChangeDetectorRef,
    private _formBuilder: FormBuilder,
    private dataService: DataService) { }

  ngOnInit() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];

    setTimeout(function () {
      // after 1000 ms we add the class animated to the login/register card
      // card.classList.remove('card-hidden');
    }, 700);

    this.subscribeForm = this._formBuilder.group({
      email: [null, Validators.email]
    });

    this.dataService.get('get-blog-category').then((result) => {
      this.categories = result.data;
      if (this.categories.length != 0 || this.categories != []) {
        const data = {
          id: this.categories[0].id
        }
        this.dataService.post('getBlogsByCategoryId', data).then((result) => {
          this.blogs = result.data
        })
      }
    })
  }

  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  subscribe() {
    this.showAlert = false;
    if (this.subscribeForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields With Correct Format',
        showConfirmButton: false,
        timer: 2500
      })  
      this.cdf.detectChanges()
      return
    }

    const formData = this.subscribeForm.getRawValue();

    const data = {
      email: formData.email
    }

    this.dataService.post('newsletter', data).then((result) => {
      if (result.success == true) {
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Success',
          showConfirmButton: false,
          timer: 2500
        })
        this.subscribeForm.controls['email'].setValue(null);
        this.cdf.detectChanges();
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: result.error.message,
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges();
      }
      this.cdf.detectChanges();
    },
      error => {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges();
      }
    )
  }

  getBlogs(index, id){
    this.activeCategoryState = index;
    this.cdf.detectChanges();
    const data = {
      id: id
    }
    this.dataService.post('getBlogsByCategoryId', data).then((result) => {
      this.blogs = result.data
      this.cdf.detectChanges()
    })
  }

  getBlogDetails(id){
    this.router.navigate(['Website/blogsdetails', id])
  }
}
