import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';
import { Store } from '@ngrx/store';
import { TranslateService } from '@ngx-translate/core';


import { locale as englishLang } from '../../i18n/en';
import { locale as frenchLang } from '../../i18n/fr';
import { locale as germanLang } from '../../i18n/gr';
import { locale as italianLang } from '../../i18n/it';
import { locale as spanishLang } from '../../i18n/sp';
import { locale as chineseLang } from '../../i18n/ch';
import { TranslationService } from '../../translation.service';
declare var $: any
@Component({
  selector: 'app-Home-cmp',
  templateUrl: './Home.component.html'
})

export class HomeComponent  implements OnInit, OnDestroy {
  test: Date = new Date();
  faqsList = [];
  faqLength = 0;
  chekfaq=2;
  constructor(private router: Router,
    private dataService: DataService,private translationService: TranslationService) {
      this.translationService.addTranslation(englishLang, frenchLang,germanLang,italianLang,spanishLang,chineseLang)

  }
  ngOnInit() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];
    setTimeout(function () {
      // after 1000 ms we add the class animated to the login/register card
      // card.classList.remove('card-hidden');
    }, 700);

    this.dataService.get('faq-list').then((result) => {
      console.log(result)
      this.faqsList = result.data;
      this.faqLength = this.faqsList.length;
     // this.faqsList = this.faqsList.slice(0, 4);
    })
  }
  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');

  }
  changehost(val) {
    this.chekfaq=val;
    if (val == 1) {
      $('#host').removeClass("btn-default bg-white text-dark");
      $('#host').addClass("btn-danger");
      $('#guest').removeClass("btn-danger");
      $('#guest').addClass("btn-default bg-white text-dark");
    } else {
      $('#guest').removeClass("btn-default bg-white text-dark");
      $('#guest').addClass("btn-danger");
      $('#host').removeClass("btn-danger");
      $('#host').addClass("btn-default bg-white text-dark");
    }
  }
}
