import { Routes } from '@angular/router';

import { HomeComponent } from './Home/Home.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { HostComponent } from './Host/Host.component';
import { ContactUsComponent } from './ContactUs/ContactUs.component';
import { BlogsComponent } from './Blogs/Blogs.component';
import { BlogsDetailsComponent } from './BlogsDetails/BlogsDetails.component';
import { PrivacyComponent } from './Privacy/Privacy.component';
import { ContentnotprovidedComponent } from './Contentnotprovided/Contentnotprovided.component';
import { FAQsComponent } from './Faqs/faqs.component';

export const WebsiteRoutes: Routes = [

    {
        path: '',
        children: [ {
            path: 'home',
            component: HomeComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'dashboard',
            component: DashboardComponent
        }]
    }
    ,
    {
        path: '',
        children: [ {
            path: 'host',
            component: HostComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'contactus',
            component: ContactUsComponent
        }]
    }
    ,
    {
        path: '',
        children: [ {
            path: 'blogs',
            component: BlogsComponent
        }]
    }
    ,   
    {
        path: '',
        children: [ {
            path: 'blogsdetails/:id',
            component: BlogsDetailsComponent
        }]
    }
    ,   
    {
        path: '',
        children: [ {
            path: 'privacy',
            component: PrivacyComponent
        }]
    },   
    {
        path: '',
        children: [ {
            path: 'contentnotprovided',
            component: ContentnotprovidedComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'faqs',
            component: FAQsComponent
        }]
    }
];
