import { Component, OnInit, OnDestroy, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DataService } from 'src/app/_services/data.services';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-BlogsDetails-cmp',
  templateUrl: './BlogsDetails.component.html'
})

export class BlogsDetailsComponent implements OnInit, OnDestroy {
  test: Date = new Date();
  reply = 0;
  sub = null;
  id = null;
  @ViewChild('subscribeNgForm') subscribeNgForm: NgForm;
  subscribeForm: FormGroup;
  showAlert = false;
  errorMessage = null;
  details = null;
  moment: any = moment;
  tags = null;
  blogs = null;
  user_id = null;
  commentsLength = null;
  comments = null;

  constructor(private cdf: ChangeDetectorRef,
    private _formBuilder: FormBuilder,
    private routers: ActivatedRoute,
    private router: Router,
    private dataService: DataService) { }

  ngOnInit() {
    if(localStorage.getItem('id')){
      this.user_id = localStorage.getItem('id')
    }

    this.sub = this.routers.params.subscribe(params => {
      this.id = params['id'];
    });

    this.getDetails(this.id)
    this.getComments(this.id)

    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];

    setTimeout(function () {
      // after 1000 ms we add the class animated to the login/register card
      // card.classList.remove('card-hidden');
    }, 700);

    this.subscribeForm = this._formBuilder.group({
      email: [null, Validators.email]
    });
  }

  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  subscribe() {
    this.showAlert = false;
    if (this.subscribeForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields With Correct Format',
        showConfirmButton: false,
        timer: 2500
      })  
      this.cdf.detectChanges()
      return
    }

    const formData = this.subscribeForm.getRawValue();

    const data = {
      email: formData.email
    }

    this.dataService.post('newsletter', data).then((result) => {
      if (result.success == true) {
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Success',
          showConfirmButton: false,
          timer: 2500
        })
        this.subscribeForm.controls['email'].setValue(null);
        this.cdf.detectChanges();
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: result.error.message,
          showConfirmButton: false,
          timer: 2500
        })  
        this.cdf.detectChanges();
      }
      this.cdf.detectChanges();
    },
      error => {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })  
        this.cdf.detectChanges();
      }
    )
  }

  getDetails(id){
    const data = {
      id: id
    }
    this.dataService.post('get-blogs-by-id', data).then((result) => {
      this.details = result.data
      this.cdf.detectChanges()
      if(this.details.tags){
        this.tags = this.details.tags.split(",")
      }
      this.cdf.detectChanges();
      const data = {
        id: this.details.blog_category.id
      }
      this.dataService.post('getBlogsByCategoryId', data).then((result) => {
        this.blogs = result.data;
        this.blogs = this.blogs.slice(0, 4);
        this.cdf.detectChanges();
      })
    })
  }

  getBlogDetails(id){
    this.router.navigate(['Website/blogsdetails', id])
  }

  postComment(id){
    if($('#comment').val() == null || $('#comment').val() == ''){
      return
    }

    const data = {
      user_id: this.user_id,
      blog_id: id,
      comment: $('#comment').val()
    }
    this.dataService.post('comment/store', data).then((result) => {
      this.getComments(id);
      this.cdf.detectChanges();
    })
  }

  replyToComment(blog_id, comment_id){
    
    if($('#reply').val() == null || $('#reply').val() == ''){
      return
    }

    const data = {
      user_id: this.user_id,
      comment_id: comment_id,
      comment: $('#reply').val()
    }
    this.dataService.post('reply/store', data).then((result) => {
      this.reply = 0
      this.getComments(blog_id);
      this.cdf.detectChanges();
    })
  }

  getComments(id){
    const data = {
      id: id
    }
    this.dataService.post('comment', data).then((result) => {
      this.comments = result.data
      this.commentsLength = this.comments.length
    })
  }
}
